...........Animation and transition effects.\n * Borders..............Various borders and divider styles.\n * Colors...............Text and background colors.\n * Display..............Show and hide and breakpoint visibility rules.\n * Filters..............CSS filters styles.\n * Spacings.............Padding and margins in classes.\n *\n * TRUMPS\n * Helper Classes.......Helper classes loaded last in the cascade.\n */\n/* ------------------------------------ *    $SETTINGS\n\\* ------------------------------------ */\n/* ------------------------------------*    $MIXINS\n\\*------------------------------------ */\n/**\n * Convert px to rem.\n *\n * @param int $size\n *   Size in px unit.\n * @return string\n *   Returns px unit converted to rem.\n */\n/**\n * Center-align a block level element\n */\n/**\n * Standard paragraph\n */\n/**\n * Maintain aspect ratio\n */\n/* ------------------------------------*    $VARIABLES\n\\*------------------------------------ */\n/**\n * Grid & Baseline Setup\n */\n/**\n * Colors\n */\n/**\n * Style Colors\n */\n/**\n * Typography\n */\n/**\n * Amimation\n */\n/**\n * Default Spacing/Padding\n */\n/**\n * Icon Sizing\n */\n/**\n * Common Breakpoints\n */\n/**\n * Element Specific Dimensions\n */\n/* ------------------------------------*    $TOOLS\n\\*------------------------------------ */\n/* ------------------------------------*    $MIXINS\n\\*------------------------------------ */\n/**\n * Convert px to rem.\n *\n * @param int $size\n *   Size in px unit.\n * @return string\n *   Returns px unit converted to rem.\n */\n/**\n * Center-align a block level element\n */\n/**\n * Standard paragraph\n */\n/**\n * Maintain aspect ratio\n */\n/* ------------------------------------*    $MEDIA QUERY TESTS\n\\*------------------------------------ */\nbody::before {\n  display: block;\n  position: fixed;\n  z-index: 100000;\n  background: black;\n  bottom: 0;\n  right: 0;\n  padding: 0.5em 1em;\n  content: 'No Media Query';\n  color: rgba(255, 255, 255, 0.75);\n  border-top-left-radius: 10px;\n  font-size: 0.75em; }\n  @media print {\n    body::before {\n      display: none; } }\n\nbody::after {\n  display: block;\n  position: fixed;\n  height: 5px;\n  bottom: 0;\n  left: 0;\n  right: 0;\n  z-index: 100000;\n  content: '';\n  background: black; }\n  @media print {\n    body::after {\n      display: none; } }\n\n@media (min-width: 351px) {\n  body::before {\n    content: 'xsmall: 350px'; }\n  body::after, body::before {\n    background: dodgerblue; } }\n\n@media (min-width: 501px) {\n  body::before {\n    content: 'small: 500px'; }\n  body::after, body::before {\n    background: darkseagreen; } }\n\n@media (min-width: 701px) {\n  body::before {\n    content: 'medium: 700px'; }\n  body::after, body::before {\n    background: lightcoral; } }\n\n@media (min-width: 901px) {\n  body::before {\n    content: 'large: 900px'; }\n  body::after, body::before {\n    background: mediumvioletred; } }\n\n@media (min-width: 1101px) {\n  body::before {\n    content: 'xlarge: 1100px'; }\n  body::after, body::before {\n    background: hotpink; } }\n\n@media (min-width: 1301px) {\n  body::before {\n    content: 'xxlarge: 1300px'; }\n  body::after, body::before {\n    background: orangered; } }\n\n@media (min-width: 1501px) {\n  body::before {\n    content: 'xxxlarge: 1400px'; }\n  body::after, body::before {\n    background: dodgerblue; } }\n\n/* ------------------------------------*    $GENERIC\n\\*------------------------------------ */\n/* ------------------------------------*    $RESET\n\\*------------------------------------ */\n/* Border-Box http:/paulirish.com/2012/box-sizing-border-box-ftw/ */\n* {\n  -moz-box-sizing: border-box;\n  -webkit-box-sizing: border-box;\n  box-sizing: border-box; }\n\nbody {\n  margin: 0;\n  padding: 0; }\n\nblockquote,\nbody,\ndiv,\nfigure,\nfooter,\nform,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\nheader,\nhtml,\niframe,\nlabel,\nlegend,\nli,\nnav,\nobject,\nol,\np,\nsection,\ntable,\nul {\n  ma