ow: none !important;\n  }\n\n  a,\n  a:visited {\n    text-decoration: underline;\n  }\n\n  a[href]::after {\n    content: \" (\" attr(href) \")\";\n  }\n\n  abbr[title]::after {\n    content: \" (\" attr(title) \")\";\n  }\n\n  /*\n   * Don't show links that are fragment identifiers,\n   * or use the `javascript:` pseudo protocol\n   */\n\n  a[href^=\"#\"]::after,\n  a[href^=\"javascript:\"]::after {\n    content: \"\";\n  }\n\n  blockquote,\n  pre {\n    border: 1px solid #ececec;\n    page-break-inside: avoid;\n  }\n\n  /*\n   * Printing Tables:\n   * http://css-discuss.incutio.com/wiki/Printing_Tables\n   */\n\n  thead {\n    display: table-header-group;\n  }\n\n  img,\n  tr {\n    page-break-inside: avoid;\n  }\n\n  img {\n    max-width: 100% !important;\n  }\n\n  h2,\n  h3,\n  p {\n    orphans: 3;\n    widows: 3;\n  }\n\n  h2,\n  h3 {\n    page-break-after: avoid;\n  }\n\n  #footer,\n  #header,\n  .ad,\n  .no-print {\n    display: none;\n  }\n}\n\n/* ------------------------------------*    $TABLES\n\\*------------------------------------ */\n\ntable {\n  border-collapse: collapse;\n  border-spacing: 0;\n  width: 100%;\n  table-layout: fixed;\n}\n\nth {\n  text-align: left;\n  padding: 0.9375rem;\n}\n\ntd {\n  padding: 0.9375rem;\n}\n\n/* ------------------------------------*    $TEXT ELEMENTS\n\\*------------------------------------ */\n\n/**\n * Abstracted paragraphs\n */\n\np,\nul,\nol,\ndt,\ndd,\npre {\n  font-family: \"Raleway\", sans-serif;\n  font-weight: 400;\n  font-size: 1rem;\n  line-height: 1.625rem;\n}\n\n/**\n * Bold\n */\n\nb,\nstrong {\n  font-weight: 700;\n}\n\n/**\n * Horizontal Rule\n */\n\nhr {\n  height: 1px;\n  border: none;\n  background-color: #979797;\n  display: block;\n  margin-left: auto;\n  margin-right: auto;\n}\n\n/**\n * Abbreviation\n */\n\nabbr {\n  border-bottom: 1px dotted #ececec;\n  cursor: help;\n}\n\n/* ------------------------------------*    $LAYOUT\n\\*------------------------------------ */\n\n/* ------------------------------------*    $GRIDS\n\\*------------------------------------ */\n\n/**\n * Simple grid - keep adding more elements to the row until the max is hit\n * (based on the flex-basis for each item), then start new row.\n */\n\n.grid {\n  display: flex;\n  display: inline-flex;\n  flex-flow: row wrap;\n  margin-left: -0.625rem;\n  margin-right: -0.625rem;\n}\n\n.grid-item {\n  width: 100%;\n  box-sizing: border-box;\n  padding-left: 0.625rem;\n  padding-right: 0.625rem;\n}\n\n/**\n * Fixed Gutters\n */\n\n[class*=\"grid--\"].no-gutters {\n  margin-left: 0;\n  margin-right: 0;\n}\n\n[class*=\"grid--\"].no-gutters > .grid-item {\n  padding-left: 0;\n  padding-right: 0;\n}\n\n/**\n* 1 to 2 column grid at 50% each.\n*/\n\n.grid--50-50 > * {\n  margin-bottom: 1.25rem;\n}\n\n@media (min-width: 701px) {\n  .grid--50-50 > * {\n    width: 50%;\n    margin-bottom: 0;\n  }\n}\n\n/**\n* 1t column 30%, 2nd column 70%.\n*/\n\n.grid--30-70 {\n  width: 100%;\n  margin: 0;\n}\n\n.grid--30-70 > * {\n  margin-bottom: 1.25rem;\n  padding: 0;\n}\n\n@media (min-width: 701px) {\n  .grid--30-70 > * {\n    margin-bottom: 0;\n  }\n\n  .grid--30-70 > *:first-child {\n    width: 40%;\n    padding-left: 0;\n    padding-right: 1.25rem;\n  }\n\n  .grid--30-70 > *:last-child {\n    width: 60%;\n    padding-right: 0;\n    padding-left: 1.25rem;\n  }\n}\n\n/**\n * 3 column grid\n */\n\n.grid--3-col {\n  justify-content: center;\n}\n\n.grid--3-col > * {\n  width: 100%;\n  margin-bottom: 1.25rem;\n}\n\n@media (min-width: 501px) {\n  .grid--3-col > * {\n    width: 50%;\n  }\n}\n\n@media (min-width: 901px) {\n  .grid--3-col > * {\n    width: 33.3333%;\n    margin-bottom: 0;\n  }\n}\n\n.grid--3-col--at-small > * {\n  width: 100%;\n}\n\n@media (min-width: 501px) {\n  .grid--3-col--at-small {\n    width: 100%;\n  }\n\n  .grid--3-col--at-small > * {\n    width: 33.3333%;\n  }\n}\n\n/**\n * 4 column grid\n */\n\n.grid--4-col {\n  width: 100%;\n}\n\n@media (min-width: 701px) {\n  .grid--4-col > * {\n    width: 50%;\n  }\n}\n\n@media (min-width: 901px) {\n  .grid--4-col > * {\n    width: 25%;\n  }\n}\n\n/**\n * Full column grid\n */\n\n.grid--full {\n  display: flex;\n  justify-content: stretch;\n  flex-direction: row;\n  position: relative;\n}\n\n.grid--full > * {\n  margin: 0.625rem 0;\n}\n\n@media (min-width: 501px) {\n  .grid--full {\n    width: 100%;\n  }\n\n  .grid--full > * {\n    width: 50%;\n  }\n}\n\n@media (min-width: 901px) {\n  .grid--full > * {\n    width: 33.33%;\n  }\n}\n\n@media (min-width: 1101px) {\n  .grid--full > * {\n    width: 25%;\n  }\n}\n\n/* ------------------------------------*    $WRAPPERS & CONTAINERS\n\\*------------------------------------ */\n\n/**\n * Layout containers - keep content centered and within a maximum width. Also\n * adjusts left and right padding as the viewport widens.\n */\n\n.layout-container {\n  max-width: 81.25rem;\n  margin: 0 auto;\n  position: relative;\n  padding-left: 1.25rem;\n  padding-right: 1.25rem;\n}\n\n/**\n * Wrapping element to keep content contained and centered.\n */\n\n.wrap {\n  max-width: 81.25rem;\n  margin: 0 auto;\n}\n\n.wrap--2-col {\n  display: flex;\n  flex-direction: column;\n  flex-wrap: nowrap;\n  justify-content: flex-start;\n}\n\n@media (min-width: 1101px) {\n  .wrap--2-col {\n    flex-direction: row;\n  }\n}\n\n@media (min-width: 1101px) {\n  .wrap--2-col .shift-left {\n    width: calc(100% - 320px);\n    padding-right: 1.25rem;\n  }\n}\n\n.wrap--2-col .shift-right {\n  margin-top: 2.5rem;\n}\n\n@media (min-width: 701px) {\n  .wrap--2-col .shift-right {\n    padding-left: 10.625rem;\n  }\n}\n\n@media (min-width: 1101px) {\n  .wrap--2-col .shift-right {\n    width: 20rem;\n    padding-left: 1.25rem;\n    margin-top: 0;\n  }\n}\n\n.wrap--2-col--small {\n  display: flex;\n  flex-direction: column;\n  flex-wrap: nowrap;\n  justify-content: flex-start;\n  position: relative;\n}\n\n@media (min-width: 701px) {\n  .wrap--2-col--small {\n    flex-direction: row;\n  }\n}\n\n.wrap--2-col--small .shi