th--none {\n  max-width: none;\n}\n\n.height--zero {\n  height: 0;\n}\n\n.height--100vh {\n  height: 100vh;\n  min-height: 15.625rem;\n}\n\n.height--60vh {\n  height: 60vh;\n  min-height: 15.625rem;\n}\n\n","/* ------------------------------------*\\\n    $MIXINS\n\\*------------------------------------ */\n\n/**\n * Convert px to rem.\n *\n * @param int $size\n *   Size in px unit.\n * @return string\n *   Returns px unit converted to rem.\n */\n@function rem($size) {\n  $remSize: $size / $rembase;\n\n  @return #{$remSize}rem;\n}\n\n/**\n * Center-align a block level element\n */\n@mixin center-block {\n  display: block;\n  margin-left: auto;\n  margin-right: auto;\n}\n\n/**\n * Standard paragraph\n */\n@mixin p {\n  font-family: $font-primary;\n  font-weight: 400;\n  font-size: rem(16);\n  line-height: rem(26);\n}\n\n/**\n * Maintain aspect ratio\n */\n@mixin aspect-ratio($width, $height) {\n  position: relative;\n\n  &::before {\n    display: block;\n    content: \"\";\n    width: 100%;\n    padding-top: ($height / $width) * 100%;\n  }\n\n  > .ratio-content {\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n  }\n}\n","@import \"tools.mixins\";\n\n/* ------------------------------------*\\\n    $VARIABLES\n\\*------------------------------------ */\n\n/**\n * Grid & Baseline Setup\n */\n$fontpx: 16; // Font size (px) baseline applied to <body> and converted to %.\n$defaultpx: 16; // Browser default px used for media queries\n$rembase: 16; // 16px = 1.00rem\n$max-width-px: 1300;\n$max-width: rem($max-width-px) !default;\n\n/**\n * Colors\n */\n$white: #fff;\n$black: #393939;\n$off-white: #f7f8f3;\n$gray: #979797;\n$gray-light: #ececec;\n$gray-med: #9b9b9b;\n$bronze: #cd7232;\n$teal: #9fd2cb;\n$error: #f00;\n$valid: #089e00;\n$warning: #fff664;\n$information: #000db5;\n\n/**\n * Style Colors\n */\n$primary-color: $black;\n$secondary-color: $white;\n$background-color: $off-white;\n$link-color: $primary-color;\n$link-hover: $gray;\n$button-color: $primary-color;\n$button-hover: black;\n$body-color: $black;\n$border-color: $gray-light;\n$overlay: rgba(25, 25, 25, 0.6);\n\n/**\n * Typography\n */\n$font: Georgia, Times, \"Times New Roman\", serif;\n$font-primary: \"Raleway\", sans-serif;\n$font-secondary: \"Bromello\", Georgia, Times, \"Times New Roman\", serif;\n$sans-serif: \"Helvetica\", \"Arial\", sans-serif;\n$serif: Georgia, Times, \"Times New Roman\", serif;\n$monospace: Menlo, Monaco, \"Courier New\", \"Courier\", monospace;\n\n// Questa font weights: 400 700 900\n\n/**\n * Amimation\n */\n$cubic-bezier: cubic-bezier(0.885, -0.065, 0.085, 1.02);\n$ease-bounce: cubic-bezier(0.3, -0.14, 0.68, 1.17);\n\n/**\n * Default Spacing/Padding\n */\n$space: 1.25rem;\n$space-and-half: $space*1.5;\n$space-double: $space*2;\n$space-quad: $space*4;\n$space-half: $space/2;\n$pad: 1.25rem;\n$pad-and-half: $pad*1.5;\n$pad-double: $pad*2;\n$pad-half: $pad/2;\n$pad-quarter: $pad/4;\n$pad-triple: $pad*3;\n$pad-quad: $pad*4;\n$gutters: (mobile: 10, desktop: 10, super: 10);\n$verticalspacing: (mobile: 20, desktop: 30);\n\n/**\n * Icon Sizing\n */\n$icon-xsmall: rem(15);\n$icon-small: rem(20);\n$icon-medium: rem(30);\n$icon-large: rem(50);\n$icon-xlarge: rem(80);\n\n/**\n * Common Breakpoints\n */\n$xsmall: 350px;\n$small: 500px;\n$medium: 700px;\n$large: 900px;\n$xlarge: 1100px;\n$xxlarge: 1300px;\n$xxxlarge: 1500px;\n\n$breakpoints: (\n  'xsmall': $xsmall,\n  'small': $small,\n  'medium': $medium,\n  'large': $large,\n  'xlarge': $xlarge,\n  'xxlarge': $xxlarge,\n  'xxxlarge': $xxxlarge\n);\n\n/**\n * Element Specific Dimensions\n */\n$article-max: rem(950);\n$sidebar-width: 320;\n$utility-header-height: 40;\n$small-header-height: 60;\n$large-header-height: 150;\n","/* ------------------------------------*\\\n    $MEDIA QUERY TESTS\n\\*------------------------------------ */\n@if $tests == true {\n  body {\n    &::before {\n      display: block;\n      position: fixed;\n      z-index: 100000;\n      background: