---------------- */\n","/* ------------------------------------*\\\n    $LINKS\n\\*------------------------------------ */\na {\n  text-decoration: none;\n  color: $link-color;\n  transition: all 0.6s ease-out;\n  cursor: pointer !important;\n\n  &:hover {\n    text-decoration: none;\n    color: $link-hover;\n  }\n\n  p {\n    color: $body-color;\n  }\n}\n\na.text-link {\n  text-decoration: underline;\n  cursor: pointer;\n}\n","/* ------------------------------------*\\\n    $LISTS\n\\*------------------------------------ */\nol,\nul {\n  margin: 0;\n  padding: 0;\n  list-style: none;\n}\n\n/**\n * Definition Lists\n */\ndl {\n  overflow: hidden;\n  margin: 0 0 $space;\n}\n\ndt {\n  font-weight: bold;\n}\n\ndd {\n  margin-left: 0;\n}\n","/* ------------------------------------*\\\n    $SITE MAIN\n\\*------------------------------------ */\n\nhtml,\nbody {\n  width: 100%;\n  height: 100%;\n}\n\nbody {\n  background: $background-color;\n  font: 400 100%/1.3 $font-primary;\n  -webkit-text-size-adjust: 100%;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n  color: $body-color;\n  overflow-x: hidden;\n}\n\nbody#tinymce {\n  & > * + * {\n    margin-top: $space;\n  }\n\n  ul {\n    list-style-type: disc;\n    margin-left: $space;\n  }\n}\n\n.main {\n  padding-top: rem(80);\n\n  @include media('>large') {\n    padding-top: rem(100);\n  }\n}\n\n.single:not('single-work') {\n  .footer {\n    margin-bottom: rem(40);\n  }\n\n  &.margin--80 {\n    .footer {\n      margin-bottom: rem(80);\n    }\n  }\n}\n","/* ------------------------------------*\\\n    $MEDIA ELEMENTS\n\\*------------------------------------ */\n\n/**\n * Flexible Media\n */\niframe,\nimg,\nobject,\nsvg,\nvideo {\n  max-width: 100%;\n  border: none;\n}\n\nimg[src$=\".svg\"] {\n  width: 100%;\n}\n\npicture {\n  display: block;\n  line-height: 0;\n}\n\nfigure {\n  max-width: 100%;\n\n  img {\n    margin-bottom: 0;\n  }\n}\n\n.fc-style,\nfigcaption {\n  font-weight: 400;\n  color: $gray;\n  font-size: rem(14);\n  padding-top: rem(3);\n  margin-bottom: rem(5);\n}\n\n.clip-svg {\n  height: 0;\n}\n\n/* ------------------------------------*\\\n    $PRINT STYLES\n\\*------------------------------------ */\n@media print {\n  *,\n  *::after,\n  *::before,\n  *::first-letter,\n  *::first-line {\n    background: transparent !important;\n    color: $black !important;\n    box-shadow: none !important;\n    text-shadow: none !important;\n  }\n\n  a,\n  a:visited {\n    text-decoration: underline;\n  }\n\n  a[href]::after {\n    content: \" (\" attr(href) \")\";\n  }\n\n  abbr[title]::after {\n    content: \" (\" attr(title) \")\";\n  }\n\n  /*\n   * Don't show links that are fragment identifiers,\n   * or use the `javascript:` pseudo protocol\n   */\n  a[href^=\"#\"]::after,\n  a[href^=\"javascript:\"]::after {\n    content: \"\";\n  }\n\n  blockquote,\n  pre {\n    border: 1px solid $border-color;\n    page-break-inside: avoid;\n  }\n\n  /*\n   * Printing Tables:\n   * http://css-discuss.incutio.com/wiki/Printing_Tables\n   */\n  thead {\n    display: table-header-group;\n  }\n\n  img,\n  tr {\n    page-break-inside: avoid;\n  }\n\n  img {\n    max-width: 100% !important;\n  }\n\n  h2,\n  h3,\n  p {\n    orphans: 3;\n    widows: 3;\n  }\n\n  h2,\n  h3 {\n    page-break-after: avoid;\n  }\n\n  #footer,\n  #header,\n  .ad,\n  .no-print {\n    display: none;\n  }\n}\n","/* ------------------------------------*\\\n    $TABLES\n\\*------------------------------------ */\ntable {\n  border-collapse: collapse;\n  border-spacing: 0;\n  width: 100%;\n  table-layout: fixed;\n}\n\nth {\n  text-align: left;\n  padding: rem(15);\n}\n\ntd {\n  padding: rem(15);\n}\n","/* ------------------------------------*\\\n    $TEXT ELEMENTS\n\\*------------------------------------ */\n\n/**\n * Abstracted paragraphs\n */\np,\nul,\nol,\ndt,\ndd,\npre {\n  @include p;\n}\n\n/**\n * Bold\n */\nb,\nstrong {\n  font-weight: 700;\n}\n\n/**\n * Horizontal Rule\n */\nhr {\n  height: 1px;\n  border: none;\n  background-color: $gray;\n\n  @include center-block;\n}\n\n/**\n * Abbreviation\n */\nabbr {\n  border-bottom: 1px dotted $border-color;\n  cursor: help;\n}\n","/* ------------------------------------*\\\n    $GRIDS\n\\*------------------------------------ */\n\n/**\n * Simple grid - keep adding more elements to the row until the max is hit\n * (based on the flex-basis for each item), then start new row.\n */\n\n@mixin layout-in-column {\n  margin-left: -1 * $space-half;\n  margin-right: -1 * $space-half;\n}\n\n@mixin column-gutters() {\n  padding-left: $pad-half;\n  padding-right: $pad-half;\n}\n\n.grid {\n  display: flex;\n  display: inline-flex;\n  flex-flow: row wrap;\n\n  @include layout-in-column;\n}\n\n.grid-item {\n  width: 100%;\n  box-sizing: border-box;\n\n  @include column-gutters();\n}\n\n/**\n * Fixed Gutters\n */\n[class*=\"grid--\"] {\n  &.no-gutters {\n    margin-left: 0;\n    margin-right: 0;\n\n    > .grid-item {\n      padding-left: 0;\n      padding-right: 0;\n    }\n  }\n}\n\n/**\n* 1 to 2 column grid at 50% each.\n*/\n.grid--50-50 {\n  > * {\n    margin-bottom: $space;\n  }\n\n  @include media ('>medium') {\n    > * {\n      width: 50%;\n      margin-bottom: 0;\n    }\n  }\n}\n\n/**\n* 1t column 30%, 2nd column 70%.\n*/\n.grid--30-70 {\n  width: 100%;\n  margin: 0;\n\n  > * {\n    margin-bottom: $space;\n    padding: 0;\n  }\n\n  @include media ('>medium') {\n    > * {\n      margin-bottom: 0;\n\n      &:first-child {\n        width: 40%;\n        padding-left: 0;\n        padding-right: $pad;\n      }\n\n      &:last-child {\n        width: 60%;\n        padding-right: 0;\n        padding-left: $pad;\n      }\n    }\n  }\n}\n\n/**\n * 3 column grid\n */\n.grid--3-col {\n  justify-content: center;\n\n  > * {\n    width: 100%;\n    margin-bottom: $space;\n  }\n\n  @include media ('>small') {\n    > * {\n      width: 50%;\n    }\n  }\n\n  @include media ('>large') {\n    > * {\n      width: 33.3333%;\n      margin-bottom: 0;\n    }\n  }\n}\n\n.grid--3-col--at-small {\n  > * {\n    width: 100%;\n  }\n\n  @include media ('>small') {\n    width: 100%;\n\n    > * {\n      width: 33.3333%;\n    }\n  }\n}\n\n/**\n * 4 column grid\n */\n.grid--4-col {\n  width: 100%;\n\n  @include media ('>medium') {\n    > * {\n      width: 50%;\n    }\n  }\n\n  @include media ('>large') {\n    > * {\n      width: 25%;\n    }\n  }\n}\n\n/**\n * Full column grid\n */\n.grid--full {\n  display: flex;\n  justify-content: stretch;\n  flex-direction: row;\n  position: relative;\n\n  > * {\n    margin: $space-half 0;\n  }\n\n  @include media ('>small') {\n    width: 100%;\n\n    > * {\n      width: 50%;\n    }\n  }\n\n  @include media ('>large') {\n    > * {\n      width: 33.33%;\n    }\n  }\n\n  @include media ('>xlarge') {\n    > * {\n      width: 25%;\n    }\n  }\n}\n","/* ------------------------------------*\\\n    $WRAPPERS & CONTAINERS\n\\*------------------------------------ */\n\n/**\n * Layout containers - keep content centered and within a maximum width. Also\n * adjusts left and right padding as the viewport widens.\n */\n.layout-container {\n  max-width: $max-width;\n  margin: 0 auto;\n  position: relative;\n  padding-left: $pad;\n  padding-right: $pad;\n}\n\n/**\n * Wrapping element to keep content contained and centered.\n */\n.wrap {\n  max-width: $max-width;\n  margin: 0 auto;\n}\n\n.wrap--2-col {\n  display: flex;\n  flex-direction: column;\n  flex-wrap: nowrap;\n  justify-content: flex-start;\n\n  @include media('>xlarge') {\n    flex-direction: row;\n  }\n\n  .shift-left {\n    @include media('>xlarge') {\n      width: calc(100% - 320px);\n      padding-right: $pad;\n    }\n  }\n\n  .shift-right {\n    margin-top: $space-double;\n\n    @include media('>medium') {\n      padding-left: rem(170);\n    }\n\n    @include media('>xlarge') {\n      width: rem(320);\n      padding-left: $pad;\n      margin-top: 0;\n    }\n  }\n}\n\n.wrap--2-col--small {\n  display: flex;\n  flex-direction: column;\n  flex-wrap: nowrap;\n  justify-content: flex-start;\n  position: relative;\n\n  @include media('>medium') {\n    flex-direction: row;\n  }\n\n  .shift-left--small {\n    width: rem(150);\n    flex-direction: column;\n    justify-content: flex-start;\n    align-items: center;\n    text-align: center;\n    display: none;\n\n    @include media('>medium') {\n      padding-right: $pad;\n      display: flex;\n    }\n  }\n\n  .shift-right--small {\n    width: 100%;\n\n    @include media('>medium') {\n      padding-left: $pad;\n      width: calc(100% - 150px);\n    }\n  }\n}\n\n.shift-left--small.sticky-is-active {\n  max-width: rem(150) !important;\n}\n\n/**\n * Wrapping element to keep content contained and centered at narrower widths.\n */\n.narrow {\n  max-width: rem(800);\n\n  @include center-block;\n}\n\n.narrow--xs {\n  max-width: rem(500);\n}\n\n.narrow--s {\n  max-width: rem(600);\n}\n\n.narrow--m {\n  max-width: rem(700);\n}\n\n.narrow--l {\n  max-width: $article-max;\n}\n\n.narrow--xl {\n  max-width: rem(1100);\n}\n","/* ------------------------------------*\\\n    $TEXT TYPES\n\\*------------------------------------ */\n\n/**\n * Text Primary\n */\n@mixin font--primary--xl() {\n  font-size: rem(24);\n  line-height: rem(28);\n  font-family: $font-primary;\n  font-weight: 400;\n  letter-spacing: 4.5px;\n  text-transform: uppercase;\n\n  @include media ('>large') {\n    font-size: rem(30);\n    line-height: rem(34);\n  }\n\n  @include media ('>xlarge') {\n    font-size: rem(36);\n    line-height: rem(40);\n  }\n}\n\n.font--primary--xl,\nh1 {\n  @include font--primary--xl;\n}\n\n@mixin font--primary--l() {\n  font-size: rem(14);\n  line-height: rem(18);\n  font-family: $font-primary;\n  font-weight: 500;\n  letter-spacing: 2px;\n  text-transform: uppercase;\n\n  @include media ('>large') {\n    font-size: rem(16);\n    line-height: rem(20);\n  }\n}\n\n.font--primary--l,\nh2 {\n  @include font--primary--l;\n}\n\n@mixin font--primary--m() {\n  font-size: rem(16);\n  line-height: rem(20);\n  font-family: $font-primary;\n  font-weight: 500;\n  letter-spacing: 2px;\n  text-transform: uppercase;\n\n  @include media ('>large') {\n    font-size: rem(18);\n    line-height: rem(22);\n  }\n}\n\n.font--primary--m,\nh3 {\n  @include font--primary--m;\n}\n\n@mixin font--primary--s() {\n  font-size: rem(12);\n  line-height: rem(16);\n  font-family: $font-primary;\n  font-weight: 500;\n  letter-spacing: 2px;\n  text-transform: uppercase;\n\n  @include media ('>large') {\n    font-size: rem(14);\n    line-height: rem(18);\n  }\n}\n\n.font--primary--s,\nh4 {\n  @include font--primary--s;\n}\n\n@mixin font--primary--xs() {\n  font-size: rem(11);\n  line-height: rem(15);\n  font-family: $font-primary;\n  font-weight: 700;\n  letter-spacing: 2px;\n  text-transform: uppercase;\n}\n\n.font--primary--xs,\nh5 {\n  @include font--primary--xs;\n}\n\n/**\n * Text Secondary\n */\n@mixin font--secondary--xl() {\n  font-size: rem(80);\n  font-family: $font-secondary;\n  letter-spacing: normal;\n  text-transform: none;\n  line-height: 1.2;\n  // background: -webkit-linear-gradient(#d09377, #89462c);\n  // -webkit-background-clip: text;\n  // -webkit-text-fill-color: transparent;\n\n  @include media ('>large') {\n    font-size: rem(110);\n  }\n\n  @include media ('>xlarge') {\n    font-size: rem(140);\n  }\n}\n\n.font--secondary--xl {\n  @include font--secondary--xl;\n}\n\n@mixin font--secondary--l() {\n  font-size: rem(40);\n  font-family: $font-secondary;\n  letter-spacing: normal;\n  text-transform: none;\n  line-height: 1.5;\n  // background: -webkit-linear-gradient(#d09377, #89462c);\n  // -webkit-background-clip: text;\n  // -webkit-text-fill-color: transparent;\n\n  @include media ('>large') {\n    font-size: rem(50);\n  }\n\n  @include media ('>xlarge') {\n    font-size: rem(60);\n  }\n}\n\n.font--secondary--l {\n  @include font--secondary--l;\n}\n\n/**\n * Text Main\n */\n@mixin font--l() {\n  font-size: rem(80);\n  line-height: 1;\n  font-family: $font;\n  font-weight: 400;\n}\n\n.font--l {\n  @include font--l;\n}\n\n@mixin font--s() {\n  font-size: rem(14);\n  line-height: rem(16);\n  font-family: $font;\n  font-weight: 400;\n  font-style: italic;\n}\n\n.font--s {\n  @include font--s;\n}\n\n.font--sans-serif {\n  font-family: $sans-serif;\n}\n\n.font--sans-serif--small {\n  font-size: rem(12);\n  font-weight: 400;\n}\n\n/**\n * Text Transforms\n */\n.text-transform--upper {\n  text-transform: uppercase;\n}\n\n.text-transform--lower {\n  text-transform: lowercase;\n}\n\n.text-transform--capitalize {\n  text-transform: capitalize;\n}\n\n/**\n * Text Decorations\n */\n.text-decoration--underline {\n  &:hover {\n    text-decoration: underline;\n  }\n}\n\n/**\n * Font Weights\n */\n.font-weight--400 {\n  font-weight: 400;\n}\n\n.font-weight--500 {\n  font-weight: 500;\n}\n\n.font-weight--600 {\n  font-weight: 600;\n}\n\n.font-weight--700 {\n  font-weight: 700;\n}\n\n.font-weight--900 {\n  font-weight: 900;\n}\n","/* ------------------------------------*\\\n    $BLOCKS\n\\*------------------------------------ */\n\n.block__post {\n  padding: $pad;\n  border: 1px solid $gray-light;\n  transition: all 0.25s ease;\n  display: flex;\n  justify-content: space-between;\n  flex-direction: column;\n  height: 100%;\n  text-align: center;\n\n  &:hover,\n  &:focus {\n    border-color: $black;\n    color: $black;\n  }\n}\n\n.block__latest {\n  display: flex;\n  flex-direction: column;\n  cursor: pointer;\n\n  .block__link {\n    display: flex;\n    flex-direction: row;\n  }\n}\n\n.block__toolbar {\n  border-top: 1px solid $border-color;\n  margin-left: -$space;\n  margin-right: -$space;\n  margin-top: $space;\n  padding: $pad;\n  padding-bottom: 0;\n  display: flex;\n  justify-content: space-between;\n  flex-direction: row;\n\n  &--left {\n    display: flex;\n    align-items: center;\n    justify-content: flex-start;\n    font-family: sans-serif;\n    text-align: left;\n  }\n\n  &--right {\n    display: flex;\n    align-items: center;\n    justify-content: flex-end;\n  }\n}\n\n.block__toolbar-item {\n  display: flex;\n  align-items: center;\n}\n\n.block__favorite {\n  padding: $pad-half;\n}\n\n/**\n * Tooltip\n */\n.tooltip {\n  cursor: pointer;\n  position: relative;\n\n  &.is-active {\n    .tooltip-wrap {\n      display: table;\n    }\n  }\n}\n\n.tooltip-wrap {\n  display: none;\n  position: fixed;\n  bottom: 0;\n  left: 0;\n  right: 0;\n  margin: auto;\n  background-color: $white;\n  width: 100%;\n  height: auto;\n  z-index: 99999;\n  box-shadow: 1px 2px 3px rgba(black, 0.5);\n}\n\n.tooltip-item {\n  padding: $pad;\n  border-bottom: 1px solid $border-color;\n  transition: all 0.25s ease;\n  display: block;\n  width: 100%;\n\n  &:hover {\n    background-color: $gray-light;\n  }\n}\n\n.tooltip-close {\n  border: none;\n\n  &:hover {\n    background-color: $black;\n    font-size: rem(12);\n  }\n}\n\n.no-touch {\n  .tooltip-wrap {\n    top: 0;\n    left: 0;\n    width: 50%;\n    height: auto;\n  }\n}\n\n.wpulike.wpulike-heart {\n  .wp_ulike_general_class {\n    text-shadow: none;\n    background: transparent;\n    border: none;\n    padding: 0;\n  }\n\n  .wp_ulike_btn.wp_ulike_put_image {\n    padding: rem(10) !important;\n    width: rem(20);\n    height: rem(20);\n    border: none;\n\n    a {\n      padding: 0;\n      background: url('../../assets/images/icon__like.svg') center center no-repeat;\n      background-size: rem(20);\n    }\n  }\n\n  .wp_ulike_general_class.wp_ulike_is_unliked a {\n    background: url('../../assets/images/icon__like.svg') center center no-repeat;\n    background-size: rem(20);\n  }\n\n  .wp_ulike_btn.wp_ulike_put_image.image-unlike,\n  .wp_ulike_general_class.wp_ulike_is_already_liked a {\n    background: url('../../assets/images/icon__liked.svg') center center no-repeat;\n    background-size: rem(20);\n  }\n\n  .count-box {\n    font-family: $sans-serif;\n    font-size: rem(12);\n    padding: 0;\n    margin-left: rem(5);\n    color: $gray;\n  }\n}\n","/* ------------------------------------*\\\n    $BUTTONS\n\\*------------------------------------ */\n\n.btn,\nbutton,\ninput[type=\"submit\"] {\n  display: table;\n  padding: rem(13) $pad-and-half rem(12) $pad-and-half;\n  vertical-align: middle;\n  cursor: pointer;\n  color: $white;\n  background-color: $button-color;\n  box-shadow: none;\n  border: none;\n  transition: all 0.3s ease-in-out;\n  border-radius: rem(50);\n  text-align: center;\n\n  @include font--primary--xs;\n\n  &:focus {\n    outline: 0;\n  }\n\n  &:hover {\n    background-color: $button-hover;\n    color: $white;\n  }\n\n  &.center {\n    display: table;\n    width: auto;\n    padding-left: $pad;\n    padding-right: $pad;\n    margin-left: auto;\n    margin-right: auto;\n  }\n}\n\n.alm-btn-wrap {\n  margin-top: $space-double;\n\n  &::after,\n  &::before {\n    display: none;\n  }\n}\n\n.btn--outline {\n  border: 1px solid $black;\n  color: $black;\n  background: transparent;\n  position: relative;\n  padding-left: 0;\n  padding-right: 0;\n  height: rem(40);\n  width: 100%;\n  display: block;\n\n  font {\n    position: absolute;\n    bottom: rem(5);\n    left: 0;\n    right: 0;\n    width: 100%;\n  }\n\n  span {\n    font-size: rem(9);\n    display: block;\n    position: absolute;\n    top: rem(5);\n    left: 0;\n    right: 0;\n    color: $gray;\n    width: 100%;\n  }\n}\n\n.btn--download {\n  position: fixed;\n  bottom: rem(40);\n  left: 0;\n  width: 100%;\n  border-radius: 0;\n  color: white;\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n  justify-content: center;\n  border: none;\n  z-index: 9999;\n  background: url('../../assets/images/texture.jpg') center center no-repeat;\n  background-size: cover;\n\n  span,\n  font {\n    font-size: inherit;\n    color: white;\n    width: auto;\n    position: relative;\n    top: auto;\n    bottom: auto;\n  }\n\n  span {\n    padding-right: rem(5);\n  }\n}\n\n.alm-btn-wrap {\n  margin: 0;\n  padding: 0;\n}\n\nbutton.alm-load-more-btn.more {\n  width: auto;\n  border-radius: rem(50);\n  background: transparent;\n  border: 1px solid $black;\n  color: $black;\n  position: relative;\n  cursor: pointer;\n  transition: all 0.3s ease-in-out;\n  padding-left: $pad-double;\n  padding-right: $pad-double;\n  margin: 0 auto;\n  height: rem(40);\n\n  @include font--primary--xs;\n\n  &.done {\n    opacity: 0.3;\n    pointer-events: none;\n\n    &:hover {\n      background-color: transparent;\n      color: $body-color;\n    }\n  }\n\n  &:hover {\n    background-color: $button-hover;\n    color: $white;\n  }\n\n  &::after,\n  &::before {\n    display: none !important;\n  }\n}\n","/* ------------------------------------*\\\n    $MESSAGING\n\\*------------------------------------ */\n","/* ------------------------------------*\\\n    $ICONS\n\\*------------------------------------ */\n.icon {\n  display: inline-block;\n}\n\n.icon--xs {\n  width: $icon-xsmall;\n  height: $icon-xsmall;\n}\n\n.icon--s {\n  width: $icon-small;\n  height: $icon-small;\n}\n\n.icon--m {\n  width: $icon-medium;\n  height: $icon-medium;\n}\n\n.icon--l {\n  width: $icon-large;\n  height: $icon-large;\n}\n\n.icon--xl {\n  width: $icon-xlarge;\n  height: $icon-xlarge;\n}\n\n.icon--arrow {\n  background: url('../../assets/images/arrow__carousel.svg') center center no-repeat;\n}\n\n.icon--arrow.icon--arrow-prev {\n  transform: rotate(180deg);\n}\n","/* ------------------------------------*\\\n    $LIST TYPES\n\\*------------------------------------ */\n","/* ------------------------------------*\\\n    $NAVIGATION\n\\*------------------------------------ */\n\n.nav__primary {\n  display: flex;\n  flex-wrap: nowrap;\n  align-items: center;\n  width: 100%;\n  justify-content: center;\n  height: 100%;\n  max-width: $max-width;\n  margin: 0 auto;\n  position: relative;\n\n  @include media('>large') {\n    justify-content: space-between;\n  }\n\n  .primary-nav__list {\n    display: none;\n    justify-content: space-around;\n    align-items: center;\n    flex-direction: row;\n    width: 100%;\n\n    @include media('>large') {\n      display: flex;\n    }\n  }\n\n  &-mobile {\n    display: none;\n    flex-direction: column;\n    width: 100%;\n    position: absolute;\n    background-color: white;\n    top: rem($small-header-height);\n    box-shadow: 0 1px 2px rgba($black, 0.4);\n  }\n}\n\n.primary-nav__list-item {\n  &.current_page_item,\n  &.current-menu-parent {\n    > .primary-nav__link {\n      color: $gray-med;\n    }\n  }\n}\n\n.primary-nav__link {\n  padding: $pad;\n  border-bottom: 1px solid $gray-light;\n  width: 100%;\n  text-align: left;\n  font-family: $font-primary;\n  font-weight: 500;\n  font-size: rem(14);\n  text-transform: uppercase;\n  letter-spacing: rem(2);\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n\n  &:focus {\n    color: $primary-color;\n  }\n\n  @include media('>large') {\n    padding: $pad;\n    text-align: center;\n    border: none;\n  }\n}\n\n.primary-nav__subnav-list {\n  display: none;\n  background-color: rgba($gray-light, 0.4);\n\n  @include media('>large') {\n    position: absolute;\n    width: 100%;\n    min-width: rem(200);\n    background-color: white;\n    border-bottom: 1px solid $gray-light;\n  }\n\n  .primary-nav__link {\n    padding-left: $pad-double;\n\n    @include media('>large') {\n      padding-left: $pad;\n      border-top: 1px solid $gray-light;\n      border-left: 1px solid $gray-light;\n      border-right: 1px solid $gray-light;\n\n      &:hover {\n        background-color: rgba($gray-light, 0.4);\n      }\n    }\n  }\n}\n\n.primary-nav--with-subnav {\n  position: relative;\n\n  @include media('>large') {\n    border: 1px solid transparent;\n  }\n\n  > .primary-nav__link::after {\n    content: \"\";\n    display: block;\n    height: rem(10);\n    width: rem(10);\n    margin-left: rem(5);\n    background: url('../../assets/images/arrow__down--small.svg') center center no-repeat;\n  }\n\n  &.this-is-active {\n    > .primary-nav__link::after {\n      transform: rotate(180deg);\n    }\n\n    .primary-nav__subnav-list {\n      display: block;\n    }\n\n    @include media('>large') {\n      border: 1px solid $gray-light;\n    }\n  }\n}\n\n.nav__toggle {\n  position: absolute;\n  padding-right: $space-half;\n  top: 0;\n  right: 0;\n  width: rem($small-header-height);\n  height: rem($small-header-height);\n  justify-content: center;\n  align-items: flex-end;\n  flex-direction: column;\n  cursor: pointer;\n  transition: right 0.25s ease-in-out, opacity 0.2s ease-in-out;\n  display: flex;\n  z-index: 9999;\n\n  .nav__toggle-span {\n    margin-bottom: rem(5);\n    position: relative;\n\n    @include media('>medium') {\n      transition: transform 0.25s ease;\n    }\n\n    &:last-child {\n      margin-bottom: 0;\n    }\n  }\n\n  .nav__toggle-span--1,\n  .nav__toggle-span--2,\n  .nav__toggle-span--3 {\n    width: rem(40);\n    height: rem(2);\n    border-radius: rem(3);\n    background-color: $primary-color;\n    display: block;\n  }\n\n  .nav__toggle-span--1 {\n    width: rem(20);\n  }\n\n  .nav__toggle-span--2 {\n    width: rem(30);\n  }\n\n  .nav__toggle-span--4::after {\n    font-size: rem(11);\n    text-transform: uppercase;\n    letter-spacing: 2.52px;\n    content: \"Menu\";\n    display: block;\n    font-weight: 700;\n    line-height: 1;\n    margin-top: rem(3);\n    color: $primary-color;\n  }\n\n  @include media('>large') {\n    display: none;\n  }\n}\n","/* ------------------------------------*\\\n    $PAGE SECTIONS\n\\*------------------------------------ */\n\n.section--padding {\n  padding: $pad-double 0;\n}\n\n.section__main {\n  padding-bottom: $pad-double;\n}\n\n.section__hero + .section__main {\n  padding-top: $pad-double;\n}\n\n.section__hero {\n  padding: $pad-double 0;\n  min-height: rem(400);\n  margin-top: rem(-40);\n  width: 100%;\n  text-align: center;\n  display: flex;\n  justify-content: center;\n  background-attachment: fixed;\n\n  @include media('>large') {\n    margin-top: rem(-60);\n  }\n\n  &.background-image--default {\n    background-image: url('../../assets/images/hero-banner.png');\n  }\n}\n\n.section__hero--inner {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  padding: $pad;\n\n  .divider {\n    margin-top: $space;\n    margin-bottom: $space-half;\n  }\n}\n\n.section__hero-excerpt {\n  max-width: rem(700);\n}\n\n.section__hero-title {\n  text-transform: capitalize;\n}\n\n/**\n * Accordion\n */\n\n.accordion-item {\n  padding-top: rem(15);\n\n  &.is-active {\n    .accordion-item__toggle {\n      background: url('../../assets/images/icon__minus.svg') no-repeat center center;\n    }\n\n    .accordion-item__body {\n      height: auto;\n      opacity: 1;\n      visibility: visible;\n      padding-top: $pad;\n      padding-bottom: $pad-double;\n    }\n\n    .accordion-item__toggle::before {\n      left: rem(-80);\n      content: \"collapse\";\n    }\n\n    &:last-child {\n      .accordion-item__body {\n        padding-bottom: $pad-half;\n      }\n    }\n  }\n}\n\n.accordion-item__title {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  cursor: pointer;\n  border-bottom: 1px solid $gray;\n  padding-bottom: $pad-half;\n}\n\n.accordion-item__toggle {\n  width: rem(20);\n  height: rem(20);\n  min-width: rem(20);\n  background: url('../../assets/images/icon__plus.svg') no-repeat center center;\n  background-size: rem(20);\n  margin: 0 !important;\n  position: relative;\n\n  &::before {\n    display: flex;\n    position: absolute;\n    left: rem(-65);\n    top: rem(4);\n    content: \"expand\";\n    color: $gray;\n\n    @include font--primary--xs;\n  }\n}\n\n.accordion-item__body {\n  height: 0;\n  opacity: 0;\n  visibility: hidden;\n  position: relative;\n  overflow: hidden;\n}\n\n/**\n * Steps\n */\n.step {\n  counter-reset: item;\n}\n\n.step-item {\n  display: flex;\n  flex-direction: row;\n  align-items: flex-start;\n  counter-increment: item;\n  margin-bottom