ill::after {\n    content: \"see all skill levels\";\n  }\n}\n\n.filter-items {\n  display: none;\n  margin-top: $space;\n\n  @include media('>large') {\n    display: flex;\n    flex-direction: column;\n    margin-bottom: rem(15);\n  }\n}\n\n.filter-item {\n  display: flex;\n  justify-content: flex-start;\n  align-items: center;\n  margin-top: $space-half;\n  position: relative;\n}\n\n.filter-footer {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  flex-direction: column;\n  width: 100%;\n  padding: $pad;\n  padding-bottom: $pad-half;\n  background: $white;\n  box-shadow: 0 -0.5px 2px rgba(black, 0.1);\n\n  @include media('>large') {\n    flex-direction: row;\n    box-shadow: none;\n    padding-bottom: $pad;\n  }\n}\n\n.filter-apply {\n  width: 100%;\n  text-align: center;\n\n  @include media('>large') {\n    min-width: rem(250);\n    width: auto;\n  }\n}\n\n.filter-clear {\n  padding: $pad-half $pad;\n  font-size: 80%;\n  text-decoration: underline;\n  border-top: 1px solid $border-color;\n  background-color: transparent;\n  width: auto;\n  color: $gray;\n  font-weight: 400;\n  box-shadow: none;\n  border: none;\n  text-transform: capitalize;\n  letter-spacing: normal;\n\n  &:hover {\n    background-color: transparent;\n    color: $black;\n  }\n}\n","/* ------------------------------------*\\\n    $SPACING\n\\*------------------------------------ */\n\n// For more information on this spacing technique, please see:\n// http://alistapart.com/article/axiomatic-css-and-lobotomized-owls.\n\n.spacing {\n  & > * + * {\n    margin-top: $space;\n  }\n}\n\n.spacing--quarter {\n  & > * + * {\n    margin-top: $space /4;\n  }\n}\n\n.spacing--half {\n  & > * + * {\n    margin-top: $space /2;\n  }\n}\n\n.spacing--one-and-half {\n  & > * + * {\n    margin-top: $space *1.5;\n  }\n}\n\n.spacing--double {\n  & > * + * {\n    margin-top: $space *2;\n  }\n}\n\n.spacing--triple {\n  & > * + * {\n    margin-top: $space *3;\n  }\n}\n\n.spacing--quad {\n  & > * + *